import { useNavigate } from "react-router-dom"
import { WATCHLIST_FEATURE_NAME } from "@rp/common/settings"
import { EmptyStateWatchlists } from "@rp/react/common/assets"
import { useIsWatchlistLimitMet } from "@rp/react/common/hooks"
import { useAccount } from "@rp/react/common/providers"
import { useModal } from "@rp/react/web/common/providers"
import { Box, Button, Typography } from "@rp/react/web/mui/core"
import { Add } from "@rp/react/web/mui/icons"
import { WATCHLIST_NEW } from "@rp/react/web/router"

const BriefNoWatchlistsCreated = () => {
  const navigate = useNavigate()

  const { quota } = useAccount()
  const {
    planComparisonModal: { handleOpen: planComparisonHandleOpen },
  } = useModal()
  const hasMetWatchlistLimit = useIsWatchlistLimitMet(quota)

  return (
    <Box sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
      <EmptyStateWatchlists
        height={230}
        width={230}
        style={{ marginTop: "24px" }}
        title="empty state watchlists"
      />

      <Typography sx={{ fontSize: "20px", lineHeight: "27px", fontWeight: 700, marginTop: "4px" }}>
        You haven't created any watchlists yet
      </Typography>

      <Typography sx={{ opacity: 0.5, lineHeight: "20px", marginTop: "2px" }}>
        Start by creating your first watchlist!
      </Typography>

      <Box sx={{ m: "28px 0" }}>
        <Button
          aria-label="add new watchlist"
          startIcon={<Add />}
          variant="contained"
          onClick={() => {
            hasMetWatchlistLimit
              ? planComparisonHandleOpen(WATCHLIST_FEATURE_NAME)
              : navigate(WATCHLIST_NEW)
          }}
        >
          New Watchlist
        </Button>
      </Box>
    </Box>
  )
}

export { BriefNoWatchlistsCreated }
